font-family: 'Open Sans', sans-serif;
font-family: 'Roboto', sans-serif; */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  /* font-family: 'Roboto', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif; */
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
font-family: 'Noto Sans', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.padding-content {
  padding-left: 20px;
  padding-right: 15px;
}

.flex {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  
}

.state-container {
  text-align: center;
  padding: 20px;
  /* background-image: linear-gradient(to right bottom,rgb(179, 179, 179), white); */
}

.state {
  font-weight: 700;
  /* display: flex; */
  margin: 0px auto;
  /* justify-content: center; */
  /* text-align: center; */
  /* align-self: center; */
  /* align-items: center; */
  /* width: 100px; */
  /* height: 100px; */
  font-size: 9rem;
  padding: 10px;
  /* border-radius: 50%; */
  background-image: linear-gradient(to right bottom,rgb(0, 128, 255),rgb(0, 72, 255) 10%,rgb(224, 41, 172)  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all .3s ease-in;
  animation: bg-gradient 1s infinite ease-in-out ;
}

@keyframes bg-gradient {
  0% {
    background-image: linear-gradient(to right bottom,yellow,rgb(0, 72, 255) 10%,rgb(224, 41, 172)  );
  }
  50% {
    background-image: linear-gradient(to right bottom,pink,rgb(0, 72, 255) 10%,yellow  );
  }
  100%{
    background-image: linear-gradient(to right bottom,yellow,rgb(0, 72, 255) 10%,red  );
  }
}

*::selection {
  background-color: violet;
  color: white;
  
}
.button {
  margin: 10px 15px;
  font-size: 2rem;
  min-width: 100px;
  outline: none;
  border: none;
  /* background-image: none; */
  /* transition: background 1s ease-in; */
  text-align: center;
  justify-content: center;
  display: inline-block;
  
}

.button:active {
  outline: 2px solid black;
  background-image: linear-gradient(to right bottom,rgb(0, 128, 255),rgb(0, 72, 255) 10%,rgb(224, 41, 172)  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  

}

.active {
  animation: number .5s ease-in-out;
  transition: font-size 0.5s ease-in-out;
}

@keyframes number {
  0% {
    font-size: 9rem;
  }
  50% {
    font-size: 10rem;
  }
  100% {
    font-size: 9rem;
  }
}

.text-add-button {
  padding: 10px;
  border-radius: 10px;
}
.text-add {
  text-align: center;
}
.flex1 {
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  /* background-color: black; */
}
.texts {
  margin: 5px 5px;
  text-align: center;
  font-size: 1rem;
  padding: 10px;
  
  color: white;
  width: max-content;
  border-radius: 5px;
  background-image: linear-gradient(to right bottom,rgb(0, 128, 255),rgb(0, 72, 255) 10%,rgb(224, 41, 172)  );
  /* transition: outline .3s ease-in; */
}

.texts:hover {
  outline: 2px solid black ;
  /* box-shadow: 0px 0px 10px -1px rgb(7, 106, 255); */
}

p {
  font-family: 'Open Sans',sans-serif;
}

.searchTerm{
  /* font-family: 'Courier New', Courier, monospace; */
  font-weight: 800;
  background-image: linear-gradient(to right bottom,rgb(0, 128, 255),rgb(0, 72, 255) 10%,rgb(224, 41, 172)  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-color: whitesmoke;
}

label[htmlFor="search"]{}

.searchbar {
  padding: 10px;
  background-color: rgb(123, 0, 255);
  width: max-content;
  margin: 0 auto;
  border-radius: 100px;
  color: white;
  border: 1px solid rgb(11, 0, 21);
}

.searchbar > input {
  border-radius: none;
  outline: none;
  border: none;
  background-color: inherit;
  color: inherit;
  font-size: 1rem;
}

.tags {
  display: flex;
  width: 95%;
  padding: 15px;
  border-radius: 5px;
  margin: 10px auto;
  align-items: center;
}
.caution {
  background-color: rgb(253, 253, 202);
  border-left: 5px solid rgb(214, 214, 4);
  color:  rgb(44, 44, 0);
  
}

.caution .text {
  color: rgb(44, 44, 0);
}

.tags .icon {
  margin-right: 10px;
  font-size: 2rem;
}

.warning {
  background-color: rgb(253, 202, 202);
  border-left: 5px solid rgb(214, 4, 4);
  color: rgb(44, 0, 0);
}

.tip {
  background-color: rgb(202, 231, 253);
  border-left: 5px solid rgb(4, 130, 214);
  color: rgb(0, 24, 44);
}